.App {
  margin: 30px;
}

.Carousel {
  width: 1300px;
  display: flex;
  align-items: center;

  &__window {
    height: 100%;
    overflow: hidden;
  }

  &__list {
    margin: 0;
    padding: 0;
    display: flex;
    list-style-type: none;

    transition: 500ms ease-in-out;
  }
}

.Buttons {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.SettingsCarousel {
  display: flex;
  flex-direction: column;

  &__field {
    width: 50px;
    margin-bottom: 5px;
  }

  &__submit {
    width: 100px;
  }
}

.Arrow {
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 5px;
  cursor: pointer;
  background-color: white;
  margin: 5px;

  &__left {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
  }

  &__right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }
}
